<template>
  <div class="subscribe-mantman">
    <p class="">
      Sign up to get the latest news and updates
    </p>
    <form class="subscribe-form" @submit.prevent="onSubmitSubscribe">
      <input type="email" class="email-input" name="email"
             placeholder="mantman@example.com" autocomplete="off" v-model="email" :disabled="sending">
      <button type="submit" class="subscribe-button" :disabled="sending || !isValidatedEmail">
        Subscribe
      </button>
    </form>
  </div>
</template>

<script>
import db from "../utils/firestore"

export default {
  name: "SubscribeMantman",
  data: () => ({
    email: null,
    isValidatedEmail: false,
    sending: false,
    showSuccessSnackbar: false
  }),
  watch: {
    email(value) {
      this.email = value;
      this._validateEmail(value);
    }
  },
  methods: {
    _validateEmail(value) {
      this.isValidatedEmail = (/^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(value));
    },
    _subscribeToMantman() {
      db.collection("subscribe_to_mantman")
          .add({
            email: this.email,
            created: Date.now(),
            is_validate: false,
            un_subscribe: false
          })
          .then(() => {
            this.email = null;
            this.isValidatedEmail = false;
            this.sending = false;
            this.showSuccessSnackbar = true;
            alert('Thank you *^^*');
          })
          .catch((error) => {
            alert('Error TT');
            console.error("Error writing document: ", error);
          });
    },
    onSubmitSubscribe() {
      if (this.isValidatedEmail) {
        this.sending = true;
        this._subscribeToMantman();
      }
    }
  }
}
</script>

